.search_result_container {
  display: flex;
  flex-direction: column;
  align-items: center;

  .search_result_content {
    width: 85%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .title-search {
    color: #162a4c;
    font-size: 28px;
    line-height: 33px;
    margin: 10px;
  }

  .btn-see-more {
    font-size: 17px;
    line-height: 20px;
    color: #fff;
    background: #fe5466;
    border-radius: 4px;
    margin: 30px 0;
    padding: 10px 50px;
  }

  .btn-see-more:hover {
    opacity: 0.5;
    transition: 0.5s;
  }

  @media screen and (max-width: 600px) {
    .search_result_content {
      width: 95%;
    }
  }
}
